<template>
  <ContentCard class="content">
    <ContentWrapper
      :filter="filter"
      :filterSchema="filterSchema"
      :pagination="pagination"
      @filter-change="handleFilterChange"
      @filter-reset="handleFilterReset"
    >
        <ButtonGroupForListPage 
        pageName='onclickInfo' 
        @add="handleCreate"
        @edit="handleEdit"
        @delete="handleDelete">
        </ButtonGroupForListPage>
      <Table
        :props="table.props"
        :header="table.header"
        :data="table.data"
        :selected="table.selected"
        :selection-type="table.selectionType"
        :select-on-row-click="true"
        @row-selection-add="handleRowSelectionAdd"
        @row-selection-remove="handleRowSelectionRemove"
        @all-row-selection-change="handleAllRowSelectionChange"/>
    </ContentWrapper>
  </ContentCard>
</template>

<script>
import _ from "gateschema";
import ButtonGroupForListPage from '@/components/ButtonGroupForListPage'
import ButtonList from "./../../components/ButtonLIst";
import { ContentWrapper, Table, ActionList, utils } from "admin-toolkit";
import BaseList from '@/components/BaseList'
export default {
  extends: BaseList,
  components: {
    ActionList,
    Table,
    ContentWrapper,
    ButtonGroupForListPage
  },
  data() {
    return {
      resourceType: 'clickEvent',
      resourceName: '',
      filter: {
      },
      filterSchema: null,
      pagination: {},
      setDialogVisible: false, //
      selected: [],
      table: {
        props: {},
        header: [
            {
                label: 'ID',
                prop: 'commonOnclickId'
            },
            {
                label: '',
                prop: 'onlickName'
            },
             {
                label: '',
                prop: 'remark'
            },
            {
                label: '',
                prop: 'packagename'
            },
            {
                label: '',
                prop: 'versioncode'
            },
            {
                label: '',
                prop: 'dowhat'
            },
            {
                label: '',
                prop: 'bywhat'
            },
            {
                label: '',
                prop: 'byvalue' 
            },
            {
                label: '',
                prop: 'params', 
            },
            {
                label: '',
                prop: 'exception'
            },  
            {
                label: '',
                prop: 'modifierName'
            },
            {
                label: '',
                prop: 'createdDate' 
            },
            {
                label: "",
                fixed: "modifierName"
            },
            {
                label: "",
                fixed: "lastUpdateDate"
            }
        ],
        data: [],
        selected: [],
        selectionType: 'multiple'
      }
    };
  },
  methods: {
    //
    handleFilterChange(type, filter) {
      if (filter) { this.filter = filter}
        if(this.$validateId(this.filter.commonOnclickId)) {
        if (type === 'query') {
          if (this.pagination) {
            this.pagination.currentPage = 1
          }
        }
        this.fetchData() 
      }
    },
    //
    handleFilterReset() {
      this.filter = {};
      this.pagination.currentPage = 1
      this.fetchData();
    },
    parseFilter() {
      const { filter, pagination } = this;
      if (pagination) {
        filter.page = pagination.currentPage;
        filter.rows = pagination.pageSize;
      }
      return filter;
      console.log(filter);
    },
    /**
     * 
     */
    fetchData() {
      const filter = this.parseFilter();
      this.$service.getCommonOnclickInfoList(filter).then(data => {
        this.pagination.total = data.total;
        this.table.data = data.rows;
      });
    }
  },
  created() {
    let filterSchema = _.map({
      commonOnclickId: _.o.string.other("form", {
        component: "Input",
        placeholder: "ID"
      }),
      onlickName: _.o.string.other("form", {
        component: "Input",
        placeholder: "",
      })
    })
      .other("form", {
         cols: {
        item: 6,
        label: 0,
        wrapper: 20
      },
      layout: "inline",
      footer: {

        cols: {
          label: 0,
          wrapper: 24
        },
        showSubmit: true,
        submitText: "",
        showReset: true,
        resetText: ""
      }
    });
    this.filterSchema = filterSchema;
    this.fetchData();
  }
};
</script>
<style lang = 'stylus' scoped>
.btns
  margin-bottom 10px
  display flex
  flex-direction row
</style>